// NAVIGATION TOOLBAR
#nav {
  position: static;
  top: -32px;
  width: 100%;

  &.affix {
    position: fixed;
    top: 0;
    transition: all .6s ease-in-out;
    z-index: 10; } }

.navbar-nav {
  margin: 1px 1px; }

// NAV MAIN
.navbar-custom {
  background-color: $color-white;
  border-width: 0;
  font-family: 'Lato', 'sans serif';
  font-size: 1.1em;
  font-weight: 700;
  letter-spacing: 3px;
  text-transform: uppercase;
  transition: background-color .75s ease;

  .navbar-nav > li > a {
    border: 2px solid transparent;
    color: $color-gray;
    transition: color .75s ease, border .75s ease;

    &:hover {
      background-color: $color-white-darker;
      transition: color 1.5s ease, border 1.5s ease, background-color 1.5s ease; } } }

// NAV SELECTED
.navbar-inverse {
  .navbar-nav>.active>a {
    background-color: $color-white-darker;
    color: $color-white; } }

