// 3.0.2 bug workaround
.navbar-collapse.in {
  overflow-y: visible; }

.navbar-toggle {
  outline: 0; }

// mobile nav button
#navButton {
  border-color: transparent;
  font-size: xx-large;
  margin-bottom: 1px;
  padding: 0 0 10px 42px;

  &:hover {
    background-color: $color-white; } }

.navbar-nav.nav-justified > li {
  float: none; }
